

















	export default{
		data(){
			return{
				notesInfo: [],
        nowIndex: 0,
        notesLength: 0,
        reqing: true
			}
		},
    computed: {
      showBtn() {
        if (this.reqing) {
          return false
        } else {
          if (this.nowIndex < this.notesLength - 1) {
            return true
          } else {
            return false
          }
        }
      }
    },
    onLoad() {
    	this.$http({
        url: this.$apis.articles,
        method: 'GET',
        data: {
          cat_id: 8
        },
        cb: (err, data) => {
          if (!err && data.success) {
            this.notesInfo = data.data
            this.notesInfo.forEach(item => {
              item.content = item.content.replace(/\<img/gi, '<img style="display:block;max-width:100%;height:auto" ')
            })
            this.notesLength = this.notesInfo.length
          } else {
            uni.showToast({
            	title: '请求数据失败',
              icon: 'none'
            })
          }
          this.reqing = false
        }
      })
    },
		methods:{
      prevPage() {
        if (this.nowIndex >= 1) {
          this.nowIndex --
        }
      },
      nextPage() {
        if (this.nowIndex < this.notesLength - 1) {
          this.nowIndex ++
        }
      },
      gotoBack() {
        uni.navigateBack()
      }
		}
	}
